	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapitre 2 - Perturbations en classe","texte":{"childMarkdownRemark":{"html":"<p>À mon réveil, je remarque que ma fleur a beaucoup poussé durant la nuit. Je la regarde et examine mes mains. Est-ce à cause de moi que ma fleur a autant grandi en une nuit? Je m’habille en cogitant à tout ça. Je dois trouver d’où cela provient, mais discrètement. Je ne voudrais pas que les autorités me trouvent et me dissèque comme dans les films de créatures de l’espace. Je regarde l’heure, j’ai le temps de déjeuner et de penser à tout cela avant mon départ pour l’école.</p>\n<p>Je mange mon petit déjeuner et comme à l’habitude, mon père quitte ensuite pour son travail. Il me donne un baiser sur le front avant de partir travailler. Alors que je rince ma vaisselle, ma mère arrive, finissant de mettre son veston et sa cravate. Dire qu’elle doit porter une cravate pour son travail. C’est assez rigolo sachant que c’est l’un des seuls emploie où les femmes et les hommes devaient porter des cravates et habits convenables pour travailler.</p>\n<p>Ma mère: Tu veux que je te reconduise à l’école?<br>\nGaïa: Non, merci, je vais marcher.<br>\nMa mère: Comme tu veux ma puce, à ce soir.<br>\nGaïa: À ce soir, maman.</p>\n<p>Ma mère part pour le travail. Je sors faire un tour rapide du jardin et marche vers ma serre. J’entends mes plantes, certaines veulent de l’eau et d’autres sont satisfaites de leur état. J’entre donc dans ma serre, remplis l'arrosoir et arrose rapidement mes plantes. L’heure où je dois quitter pour l’école arrive et je retourne donc chez moi prendre mon sac et bien fermer la porte à clé avant de partir pour l’école.</p>\n<p>Je dois prendre une bonne marche d’environ 15 minutes avant d’arriver à l’école. À l’heure où j’arrive, j’ai le temps de faire un tour rapide du jardin intérieur de l’école et observer les plantes qui y poussent. J’y pense, lorsque je serais au campus de ma future école de botanique, je devrai tenter de trouver quelqu’un pour prendre la relève. Soudain, je recommence à voir les plantes et leurs fibres d’énergies. Je soupire doucement et m’assieds par terre. Je regarde l’arbre du jardin et je sens l’énergie qui afflue vers celui-ci. Il semble grossir un peu. Je me lève rapidement et recule; c’est moi qui cause ça, c’est clair maintenant. Il arrête de croître lorsque je suis assez loin. Je regarde autour de moi, pour vérifier si quelqu’un a vu ce qui vient d’arriver. Des élèves passent, mais ils ne semblent pas avoir remarqué, fiou. Je sors du jardin et vais  à  mon casier pour prendre les manuels de mes cours de l’avant-midi. J’arrive dans la classe et m’installe à mon bureau. Je regarde vers la fenêtre et réfléchit à tout ce que je vis depuis peu. D’autres élèves entrent et me font un salut de la main en m'interpellant, ce qui me ramène à la réalité.</p>\n<p>Gaïa: Oh! Salut, ça va?</p>\n<p>J’échange un peu avec mes collègues de classe, mais sans plus. Les autres élèves ne tardent pas à arriver et le cours commence. J’essaie de rester attentive au cours, mais c’est difficile à cause de ce que je vis. La professeure m’interpelle, je me redresse en sursaut et la regarde.</p>\n<p>Gaïa: Oui? Pardon?<br>\nLa professeure: Tu nous suis ou tu as la tête dans les nuages?<br>\nGaïa: Pardon, j’avais la tête ailleurs.</p>\n<p>Certains élèves rigolent et d’autres ont hâte que le cours reprenne. Je regarde vers la fleur qui est sur le bord de la fenêtre; elle a poussé depuis le début du cours. Oh non, je ne comprend pas ce qui m’arrive et ça m’effraie. Je ferme les yeux et tente de me concentrer sur autre chose que la fleur.</p>\n<p>La professeure: Gaïa, suis le cours s’il te plaît.<br>\nGaïa: Pardon, j’ai mal à la tête…<br>\nLa professeure: Essaie quand même de te concentrer.</p>\n<p>Je hoche la tête, je veux bien, mais c’est dure quand tu vois une fleur grandir plus rapidement que la normale.</p>\n<p>Élève 1: Madame, vous avez vu, la fleur est bizarre!<br>\nÉlève 2: Ouais! On dirait qu’elle grossit à vue d’oeil!<br>\nLa professeure: Concentrez-vous sur le cours, les enfants, c’est juste une fleur.</p>\n<p>Je sens que je vais éternuer, j’essaie de me retenir et de couper le son avec ma main, mais sans succès. J’éternue et la fleur fait une sorte de sursaut et double de grosseur instantanément, brisant le pot dans lequel elle était. Certains élèves éclatent de rire en voyant la scène. Je sais que c’est moi qui ai causé ça. La professeure commence à s’énerver face à autant d’indiscipline venant des jeunes de 17 et 18 ans.</p>\n<p>La professeure: Ça suffit! Qu’est-ce qui vous prend! Vous êtes calmes d’habitude.</p>\n<p>La professeure semble assez exaspérée. Je prends mes choses rapidement.</p>\n<p>Gaïa: Je ne me sens pas très bien, je vais aller à l’infirmerie…</p>\n<p>La professeure n’a pas le temps de répliquer que je suis déjà hors de la classe. La professeure continue d’essayer de maîtriser la classe, mais moi, je m’enfuie aux toilettes. Je m’enferme dans une des cabines et me regarde dans mon miroir de poche.</p>\n<p>Gaïa: Qu’est-ce qui t’arrive ma grande? D’où est-ce que j’arrive à faire ça? Comment puis-je contrôler ça? Je dois trouver un moyen pour me maîtriser.</p>\n<p>J’entends quelqu’un qui entre dans les toilettes. Je me tais et ferme les yeux. J’ai des picotements dans le bout de mes doigts; je serre les poings et essaie de respirer. J’ouvre de nouveau les yeux et regarde mes mains. Mes ongles sont maintenant vert feuille. Je sors de la toilette et va me laver les mains. La fille qui était entrée dans la cabine à côté de la mienne sort elle aussi et se lave les mains à côté de moi. Elle me fait un petit sourire gêné, je lui rends le même sourire gêné et sors des toilettes. C’est rare une fille avec les cheveux aussi blond et plusieurs tatoo sur les bras, je ne pense pas l’avoir déjà vue auparavant.</p>\n<p>Je regarde l’heure, le premier cours est bientôt fini, mais avant d’aller au deuxième cours, je dois apprendre à maîtriser cette sorte d'habileté. Je vais m’installer devant le jardin intérieur, mais je n’y entre pas. Je regarde mes mains, on dirait que j’ai du vernis sur les doigts. En temps normal, j’aurais été contente d’avoir un verni d’un vert aussi beau, mais pas en ce moment. Je regarde ensuite l’arbre et me concentre pour faire pousser une feuille. Je vois la feuille qui pousse et grandit jusqu’à arriver à la taille des autres. Je soupire doucement, je pense avoir réussi. Je souris et regarde une fleur qui n’a pas encore atteint sa taille maximale. Je me concentre sur cette fleur et elle grossit un peu, et plus encore, jusqu’à ce qu’elle atteigne graduellement sa taille et qu’elle éclot. Je souris et me mords la lèvre. Je pense vraiment que je contrôle mon pouvoir.</p>\n<p>La cloche sonne, le premier cours se termine. Il me reste 15 minutes avant le début du deuxième cours.</p>"}},"chapitreAvant":"chapitre-1-spores","chapitreApres":"chapitre-3-la-nouvelle-amie","nomRoman":"la-puissance-de-la-nature","slug":"chapitre-2-perturbations-en-classe","codeChapitre":"H0006C002","equivalentUrl":"/stories/chapter/chapter-2-disruptions-in-class"}},"pathContext":{"slug":"chapitre-2-perturbations-en-classe"}}

